.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1200px;
  @media screen and (max-width: 767) {
    padding-left: 20px;
    padding-right: 20px;
  }

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    padding-left: 76px;
    padding-right: 75px;
  }
  @media screen and (min-width: 1200px) {
    padding-left: 71px;
    padding-right: 71px;
  }
}
