#movie-list {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-column-gap: 30px;
}
.movie-title {
  color: var(--brand-color);
  padding-left: 0px;
  font-weight: 600;
}

.movie-image {
  border-radius: 5px;
  width: 274px;
  height: 398px;
  margin-bottom: 10px;
}

#movie-info {
  line-height: 1, 16em;
  display: flex;
  font-size: 12px;
  color: var(--text-color-2);
  font-weight: 600;
  margin-bottom: 31px;
  flex-direction: row;
}

.movie-box {
  display: grid;
  justify-content: center;
}

@media (min-width: 280px) {
  #movie-list {
    display: grid;
    grid-template-columns: auto;
    justify-content: center;

    .movie-image {
      border-radius: 5px;
      max-width: 280px;
      height: 398px;
    }
    #movie-info {
      max-width: 280px;
    }
    .movie-title {
      max-width: 280px;
    }
  }
  .movie-box {
    padding: 20px 0px;
  }
}

@media (min-width: 768px) {
  #movie-list {
    display: grid;
    grid-template-columns: auto auto;

    .movie-image {
      border-radius: 5px;
      max-width: 293x;
      height: 398px;
    }
    #movie-info {
      max-width: 293px;
    }
  }
  .movie-title {
    max-width: 293px;
  }

  .movie-box {
    padding: 60px 0px;
  }
}

@media (min-width: 1024px) {
  #movie-list {
    display: grid;
    grid-template-columns: auto auto auto;

    .movie-image {
      border-radius: 5px;
      max-width: 274px;
      height: 398px;
    }
    #movie-info {
      max-width: 274px;
    }
    .movie-title {
      max-width: 274px;
    }

    .movie-box {
      padding: 60px 0px;
    }
  }
}
